import React, { Component, lazy } from "react";
import { Link } from "react-router-dom";
import { formatDate, localStoragedecryp, localStorageencryp } from "../../../../components/helpers/function";
import { ToastNotification } from "../../../../components/helpers/notification";
import {
  Panel,
  PanelBody,
  PanelHeader,
} from "../../../../components/panel/panel";
import { postDataNoTImeOut } from "../../../../config/axios";
import * as moment from "moment-timezone";

const HeadFormLaporanService = lazy(() =>
  import("./HeadFormLaporanService")
);

class FormLaporanService extends Component {
  constructor(props) {
    super(props);
    this.state = {
      export : false,
      isLoading: false,
      LaporanServices : []
    };
    this.handleSubmit = this.handleSubmit.bind(this);
  }
  componentDidMount() {
    let data = JSON.parse(localStoragedecryp("userdata"));
    this.setState({ username: data.user_id });
  }
  handleSubmit(data) {
    this.setState({ isLoading: true });
    let data_post = {
      tgl_awal: formatDate(
        moment(new Date(data.tgl_awal)).format("YYYY-MM-DD")
      ),
      tgl_akhir: formatDate(
        moment(new Date(data.tgl_akhir)).format("YYYY-MM-DD")
      ),
    };
    localStorageencryp('tgl_laporan', JSON.stringify(data_post));
    postDataNoTImeOut("service/get/report", data_post)
      .then((res) => {
        if (res.data.length === 0) {
          ToastNotification("info", "Data Yang Di Cari Tidak Ada !!!");
          this.setState({
            export: false,
            isLoading: false,
            LaporanServices: [],
          });
        } else {
          ToastNotification("success", "Laporan Transaksi Service Tersedia");
          localStorageencryp("tgl_laporan", JSON.stringify(data_post));
          this.setState({
            export: true,
            isLoading: false,
            LaporanServices: res.data,
          });
        }
      })
      .catch((error) => {
        ToastNotification("info", "Data Yang Di Cari Tidak Ada !!!");
        this.setState({
          export: false,
          isLoading: false,
        });
      });
  }
  render() {
    return (
      <div>
        <ol className="breadcrumb float-xl-right">
          <li className="breadcrumb-item">
            <Link to="#">Laporan</Link>
          </li>
          <li className="breadcrumb-item active">
            Laporan Service
          </li>
        </ol>
        <h1 className="page-header">Laporan Service </h1>
        <Panel>
          <PanelHeader>Laporan Service</PanelHeader>
          <PanelBody>
            {/* {console.log(this.state.hidden)} */}
            <HeadFormLaporanService
               export={this.state.export}
               isLoading={this.state.isLoading}
               LaporanServices={this.state.LaporanServices}
               onSubmit={(data) => this.handleSubmit(data)}
            />
          </PanelBody>
        </Panel>
      </div>
    );
  }
}
export default FormLaporanService;
